.container {

    margin: 0 auto;
    padding: 0 20px;

}

@media screen and (min-width: 480px) {
    .container {
        width: 480px;
    }
}

@media screen and (min-width: 768px) {
    .container {
        width: 768px;
        padding: 0 76px;
        
    }
}

@media screen and (min-width: 1024px) {
    .container {
        width: 1024px;
        padding: 0 71px;
    }
}